.form-input {
  height: 20px;
  width: 270px;
  font-size: 16px;
  padding: 7px;
  padding-left: 15px;
  color: #969696;
  outline: none;
}

.form-input:hover {
  height: 20px;
  width: 270px;
  font-size: 16px;
  padding: 7px;
  padding-left: 15px;
  color: #969696;
  outline: none;
  cursor: text;
}

.submit {
  border-radius: 25px;
  width:130px;
  margin-left: 20px;
  background-color: #2342A2;
  color: white;
  font-size: 17px;
  font-weight: bold;
  font-family: "proxima-soft";
