}

.submit:hover {
  border-radius: 25px;
  width:130px;
  margin-left: 20px;
  background-color: #2342A2;
  color: white;
  font-size: 17px;
  font-weight: bold;
  font-family: "proxima-soft";
  box-shadow: 2px 5px 10px #adadad;
